class Warrior extends Human {
    constructor(name, age, tribe, post){
        super(name, age, tribe);
        this.post = post;
        super.strength = 10;
        this.noOfKilled = 0;
    }
    get post(){
        return this.post;
    }
    set post(post){
        this.post = post;
    }
    get noOfKilled(){
        return this.noOfKilled;
    }
    set noOfKilled(value){
        this.noOfKilled += value;
    }
    // get status(){
    //     if(this.strength >= 10){
    //         return 'Healthy and strong';
    //     } else if(this.strength != 0){
    //         return 'Weak and injured';
    //     } else if(strength <= 0) { 
    //         return 'dead';
    //     }
    // }
    // Behaviours:
    kill(enemy) {
        enemy.status = 'dead'
        enemy.strength = 0;
        enemy.task = 'This warrior is dead';
        this.noOfKilled++;
    }
    fight(enemy) {
        this.strength -= 1;
        enemy.strength -= 1;
    }
    giveOrder(message, surbodinate){
        surbodinate.task = message;
    }
    train(){
        this.strength -= 1;
    }
    report(){
        return `I am ${this.post} ${this.name} and i am from tribe ${this.tribe}. \n I have a task: ${this.task}`
    }
    dismiss(surbodinate) {
        surbodinate.post = 'dismissed';
    }
}

